.header{
    position: fixed;
    top: 0;
    height: 75px;
    width: 100%;
    z-index: 10;
}
.header .logo img{
    margin-left:10px;
    margin-top: 10px;
    width: 50px;
    height: 50px;
}
.menu{
    visibility: hidden;
}
.menu-item{
    padding: 1%;
    font-size:17px;
    font-weight: lighter;
    cursor: pointer;
}
.menu-item:hover{
    font-size:18px;
    font-weight: bolder;
    transition: 0.3s; 
}
.menu-item a{
    text-decoration: none;
}
.mobileMenuShow{
    position: absolute;
    overflow: visible;
    visibility: visible;
    display: flex;
    align-items: right;
    flex-direction: column;
    width: fit-content;
    height: fit-content;
    text-align: right;
    right: 2.5em;
    top:0;
}
.mobileMenuShow .menu-item{
    padding-right: 2%;
}
.hamburger{
    visibility: visible;
    position: absolute;
    overflow: visible;
    font-size: 1.5em;
    top:0;
    right: 0;
    margin: 2%;
    padding: 1%;
    z-index: 9;
}
.mobileMenuHide{
    visibility: hidden;
    width: 0;
}
.logo{
    z-index: 10;
}
@media (min-width: 600px) {
    .hamburger{
        visibility: hidden;
        width: 0;
    }
    .menu{
        visibility: visible;
        position: fixed;
        display: flex;
        align-items: center;
        justify-content: center;
        right: 0;
        top:0;
        width: 100%;
    } 
}