html{
  overflow-y: auto;
}
body{
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: grid;
}
h1{
  font-size: 3.3em;
}
h2{
  font-size: 1.575em;
}
p{
  font-size: 1em;
}
a{
  font-size: 1em;
}
.heroDark{
  background-color: #242424;
  color: #c1c1bc;
}
.heroDark .header{
  background-color: rgba(23, 23, 23, 0.7);
}
.heroDark .mobileMenuShow{
  background-color: #292929;
}
.heroLight{
  background-color: #c1c1bc;
  color: #242424;
}
.heroLight .header{
  background-color: rgba(219, 219, 211, 0.7);
}
.heroLight .mobileMenuShow{
  background-color: #d3d3d0;
}
.moodIcon{
  margin-left:12px;
  margin-top: 75px;
  display: flex;
  padding: 1%;
}
.toggle{
  width: 20px;
}
.sunmoon{
  margin-left: 5px;
}
.heroDark a{
  text-decoration: underline;
  color: #c1c1bc;
}
.heroDark a:hover{
  color: #dadad9;
  transition: 0.5s;
}
.heroLight a{
  text-decoration: underline;
  color: #272727;
}
.heroLight a:hover{
  color: #000;
  transition: 0.5s;
}
.heroDark button{
  background-color: #000;
}
.heroDark button:hover{
  background-color: #151515;
  transition: 0.5s;
}
.heroLight button{
  background-color: #d0d0ce;
}
.heroLight button:hover{
  background-color: #e8e8e0;
  transition: 0.5s;
}
.heroDark section{
  background-color: #c1c1bc;
}
.heroLight section{
  background-color: #242424;
}
ul{
  list-style-type: none;
}
.images {
  visibility: hidden;
}
@media (min-width: 950px) {
  .images {
    visibility: visible;
  }
}
